.Container {
	margin-bottom: 45px;
}

.Title {
	margin-bottom: 15px;
	font-size: 40px;
	color: rgb(0, 0, 0);
}

.List {
	list-style-type: none;
	display: flex;
	justify-content: center;
}

.ElemList:not(:last-child){
	padding-right: 30px;
}

.BtnList {
	cursor: pointer;
	width: 80px;
	height: 30px;
	color: rgb(0, 0, 0);
}

.BtnList:hover {
	background-color: aquamarine;
	border: 0;
	border: 1px solid #000;
	border-radius: 10px;
}